<template>
    <div class="Scene" ref="scene"></div>
</template>

<script>
/* eslint-disable */
import * as THREE from 'three';
import {
    MeshLine,
    MeshLineMaterial
} from 'three.meshline';
import * as TWEEN from 'tween';
import Stats from 'stats.js';

import TrackballControls from '../lib/TrackballControls';
import OrbitControls from '../lib/OrbitControls';

import {
    throttle
} from 'lodash';

import {
    deg2rad,
    getPointInBetweenByPerc
} from '../common/utils';

let scene;
let camera;
let controls;
let renderer;

let CONTAINER;
let WIDTH;
let HEIGHT;
let isInitialized = false;
let isUserControl = false;

let mouseX = 0;
let mouseY = 0;
let conatinerHalfX;
let conatinerHalfY;

let previousStep = 0;
let currentStep = 0;
let cameraMove;
let cameraTarget;
let moveProgress = 0.5;
let moveOffset = 0.5;
let mouseConstraint = 0.8; // variation on camera Y when mousemove

let sphere;

const SPEED = 0.01;
const loadingManager = new THREE.LoadingManager();

const point0 = new THREE.Vector3(6.50, 1.30, 33.00);
const point1 = new THREE.Vector3(3.00, 2.00, 35.00);

const positions = [
    [point0, new THREE.Vector3(8.11, 3.63, 40.25), new THREE.Vector3(3.50, 2.1, 34.1)], // camera, pointA, pointB
    [point1, new THREE.Vector3(6.8, 20.1, 43.5), new THREE.Vector3(3.5, 20.1, 36.6)],
];

const stats = new Stats();

function initRenderer() {
    renderer = new THREE.WebGLRenderer({
        alpha: true,
        antialias: true,
    });
    renderer.shadowMap.soft = true;
    renderer.shadowMap.enabled = true;
    renderer.setSize(WIDTH, HEIGHT);

    CONTAINER.appendChild(renderer.domElement);
}

function onWindowResize() {
    renderer.setSize(0, 0);

    WIDTH = CONTAINER.offsetWidth;
    HEIGHT = CONTAINER.offsetHeight;

    camera.aspect = WIDTH / HEIGHT;
    camera.updateProjectionMatrix();
    renderer.setSize(WIDTH, HEIGHT);

    render();
}

function addCamera() {
    camera = new THREE.PerspectiveCamera(50, WIDTH / HEIGHT, 1, 10000);

    camera.position.copy(positions[0][1]);
    camera.lookAt(cameraTarget);

    if (isUserControl) {
        controls = new OrbitControls(camera, CONTAINER);
        controls.minDistance = 0;
        controls.maxDistance = 90;
        controls.target.copy(point0);
    }

    loadCamera();
}

function addSceneHelpers() {
    const axisHelper = new THREE.AxisHelper(10);
    scene.add(axisHelper);

    const gridHelper = new THREE.GridHelper(100, 100);
    scene.add(gridHelper);
}

function initEvents() {
    if (isUserControl) {
        controls.addEventListener('change', render);
    } else {
        document.addEventListener('mousemove', throttle(onDocumentMouseMove, 50), false);
    }

    window.addEventListener('resize', onWindowResize, false);
    document.body.addEventListener('keydown', handleKeyboard);

    loadingManager.onStart = function (url, itemsLoaded, itemsTotal) {
        console.log('Started loading file: ' + url + '.\nLoaded ' + itemsLoaded + ' of ' + itemsTotal + ' files.');
    };

    loadingManager.onLoad = function () {
        console.log('Loading complete!');
    };

    loadingManager.onProgress = function (url, itemsLoaded, itemsTotal) {
        console.log('Loading file: ' + url + '.\nLoaded ' + itemsLoaded + ' of ' + itemsTotal + ' files.');
    };
}

function render() {
    renderer.render(scene, camera);
}

function loadModels() {
    const importScale = 0.1;
    const loader = new THREE.JSONLoader(loadingManager);

    const colors = {
        basic: 0x1abc9c,
        accent: 0x3498db
    };

    const edgesMat = new THREE.LineBasicMaterial({
        color: colors.basic,
        linewidth: 2
    });

    const edgesMatAccent = new THREE.LineBasicMaterial({
        color: colors.accent,
        lineWidth: 4,
    });

    const wireframeMat = new THREE.MeshBasicMaterial({
        color: colors.basic,
        side: THREE.DoubleSide,
        transparent: true,
        opacity: 0.25,
        wireframe: true,
        blending: THREE.AdditiveBlending
    });

    const facesMat = new THREE.MeshBasicMaterial({
        color: colors.basic,
        side: THREE.DoubleSide,
        transparent: true,
        opacity: 0.2,
        //                    blending: THREE.AdditiveBlending
    });

    const facesMatAccent = new THREE.MeshBasicMaterial({
        color: colors.accent,
        side: THREE.DoubleSide,
        transparent: true,
        opacity: 0.9,
        blending: THREE.AdditiveBlending,
    });

    var lineMaterial = new MeshLineMaterial({
        useMap: false,
        color: new THREE.Color(colors.accent),
        opacity: 1,
        resolution: new THREE.Vector2(WIDTH, HEIGHT),
        sizeAttenuation: false,
        lineWidth: 10,
    });

    const partials = [{
        model: 'wpt-split-terrain-buildings.json',
        edges: true,
        wireframe: true,
        faces: true
    },
    {
        model: 'wpt-split-terrain.json',
        edges: false,
        wireframe: true,
        faces: true,
        position: new THREE.Vector3(0, -0.05, 0)
    },
    {
        model: 'wpt-split-terrain-green.json',
        edges: true,
        wireframe: false,
        faces: true
    },
    {
        model: 'wpt-split-terrain-wpt.json',
        edges: true,
        wireframe: true,
        faces: true
    },
        //                'wpt-split-wpt.json',
        //                'wpt-split-green.json',
        //                'wpt-split-roads.json'
    ];

    partials.forEach(partial => {
        loader.load(
            `/static/models/${partial.model}`,
            object => {
                let complex = new THREE.Object3D();

                const edgesGeo = new THREE.EdgesGeometry(object);
                const edges = new THREE.LineSegments(edgesGeo, edgesMat);
                const wireframe = new THREE.Mesh(object, wireframeMat);
                const faces = new THREE.Mesh(object, facesMat);

                [edges, wireframe, faces].forEach(obj => {
                    obj.scale.set(importScale, importScale, importScale);
                    if (partial.position) {
                        obj.position.copy(partial.position);
                    }
                });

                if (partial.edges) {
                    complex.add(edges);
                }

                if (partial.wireframe) {
                    complex.add(wireframe);
                }

                if (partial.faces) {
                    complex.add(faces);
                }

                scene.add(complex);
            });
    });

    //load floor
    /*loader.load(
     '/static/models/wpt-split-floor.json',
     object => {
     let  complex = new THREE.Object3D();

     const edgesGeo = new THREE.EdgesGeometry(object);
     const edges = new THREE.LineSegments(edgesGeo, edgesMatAccent);
     const faces = new THREE.Mesh(object,facesMatAccent);
     //
     complex.add(faces);

     //                    faces.position.set(0, -0.01, 0);
     faces.position.set(0, -0.8, 0);

     faces.currentHex = faces.material.color.getHex();
     //                    faces.material.color.setHex( 0x000000 );

     let off = false;
     //                    new TWEEN.Tween({t: 0}).to({t: 1}, 100)
     new TWEEN.Tween(faces.position).to({x: 0, y: -0.01, z: 0}, 350)
     .delay(1000)
     .easing(TWEEN.Easing.Quintic.InOut)
     .onUpdate(function(t) {
     //                            console.log('t', this);
     //                            faces.material.color.setHex(off ? 0x000000 : faces.currentHex );
     off = !off;
     })
     .onComplete(() => {
     faces.material.color.setHex(faces.currentHex);
     })
     .start();
     scene.add(complex);


     });*/
}

function addGui() {
    const gui = new dat.GUI();

    let variables = new function () {
        this.point0 = {
            x: point0.x,
            y: point0.y,
            z: point0.z,
        };

        this.pointA = {
            x: positions[currentStep][0].x,
            y: positions[currentStep][0].y,
            z: positions[currentStep][0].z,
        };

        this.pointB = {
            x: positions[currentStep][1].x,
            y: positions[currentStep][1].y,
            z: positions[currentStep][1].z,
        };

        //            this.x = point0.x;
        //            this.y = point0.y;
        //            this.z = point0.z;
    };

    let point0Folder = gui.addFolder('point (0,0)');
    let pointAFolder = gui.addFolder('point A');
    let pointBFolder = gui.addFolder('point B');

    point0Folder.add(variables.point0, 'x').step(0.1).onChange(setPoint0);
    point0Folder.add(variables.point0, 'y').step(0.1).onChange(setPoint0);
    point0Folder.add(variables.point0, 'z').step(0.1).onChange(setPoint0);

    pointAFolder.add(variables.pointA, 'x').step(0.1).onChange(setPointAB);
    pointAFolder.add(variables.pointA, 'y').step(0.1).onChange(setPointAB);
    pointAFolder.add(variables.pointA, 'z').step(0.1).onChange(setPointAB);

    pointBFolder.add(variables.pointB, 'x').step(0.1).onChange(setPointAB);
    pointBFolder.add(variables.pointB, 'y').step(0.1).onChange(setPointAB);
    pointBFolder.add(variables.pointB, 'z').step(0.1).onChange(setPointAB);

    //        point0Folder.open();

    function setPoint0() {
        point0.set(variables.point0.x, variables.point0.y, variables.point0.z);
        sphere.position.copy(point0);
    }

    function setPointAB() {
        positions[currentStep][0].set(variables.pointA.x, variables.pointA.y, variables.pointA.z);
        positions[currentStep][1].set(variables.pointB.x, variables.pointB.y, variables.pointB.z);
        //            setCameraMove();
    }

}

function loadGeometry() {
    const sphereGeometry = new THREE.OctahedronGeometry(0.2, 3);
    const sphereMaterial = new THREE.MeshBasicMaterial({
        color: 0xFFFFFF
    });

    sphere = new THREE.Mesh(sphereGeometry, sphereMaterial);
    sphere.position.copy(point0);

    scene.add(sphere);
}

function onDocumentMouseMove(event) {
    moveProgress = event.clientX / WIDTH;
    moveOffset = (event.clientY - HEIGHT / 2) / (HEIGHT / 2);

    let cameraStart = positions[currentStep][1];
    let cameraEnd = positions[currentStep][2];

    cameraMove = getPointInBetweenByPerc(cameraStart, cameraEnd, moveProgress);
    cameraMove.y = cameraMove.y - moveOffset * mouseConstraint;
}

function loadCamera() {
    const savedCamera = JSON.parse(localStorage.getItem('savedCamera'));
    if (savedCamera) {

        let tween = new TWEEN.Tween(camera.position).to(savedCamera.cameraPosition, 200)
            .onUpdate(function () {
                camera.position.set(this.x, this.y, this.z);
                camera.lookAt(savedCamera.targetPosition);
            }).start();
    }
}

function saveCamera() {
    console.log('saving camera');
    localStorage.savedCamera = JSON.stringify({
        cameraPosition: camera.position,
        targetPosition: controls.target
    });
}

function dumpCamera() {
    console.log('camera position:',
        `${camera.position.x.toFixed(2)}, ${camera.position.y.toFixed(2)}, ${camera.position.z.toFixed(2)}`);
}

function handleKeyboard(e) {
    switch (e.keyCode) {
        case 83:
            saveCamera();
            break;
        case 65:
            loadCamera();
            break;
        case 68:
            dumpCamera();
            break;
    }
}

function setCameraMove() {
    let cameraStart = positions[currentStep][1];
    let cameraEnd = positions[currentStep][2];
    cameraMove = getPointInBetweenByPerc(cameraStart, cameraEnd, moveProgress);
}

export default {
    name: 'threeDebug',
    props: ['isActive', 'step'],
    data() {
        return {};
    },
    watch: {
        step: function (val) {
            currentStep = val || 0;
            
            setCameraMove();
            new TWEEN.Tween(cameraTarget)
                .to(positions[currentStep][0], 2500)
                .easing(TWEEN.Easing.Quadratic.InOut)
                // .delay(3000)
                // .onComplete(() => {
                // })
                .start();
        }
    },
    mounted() {
        if (this.$refs.scene.querySelector('canvas') === null) {
            CONTAINER = this.$refs.scene;
            WIDTH = CONTAINER.offsetWidth;
            HEIGHT = CONTAINER.offsetHeight;

            currentStep = this.step;
            cameraTarget = new THREE.Vector3().copy(positions[0][0]);
            setCameraMove();

            if (!isInitialized) {
                this.init();
                isInitialized = true;
            }
        }
    },
    methods: {
        animate() {
            if (this.isActive) {
                if (!!cameraMove && !isUserControl) {
                    let dir = new THREE.Vector3().subVectors(cameraMove, camera.position);
                    camera.position.x += dir.x * SPEED;
                    camera.position.y += dir.y * SPEED;
                    camera.position.z += dir.z * SPEED;

                    camera.lookAt(cameraTarget);
                }
                if (isUserControl) {
                    controls.update();
                }


                stats.begin();
                TWEEN.update();
                render();
                stats.end();
            }

            requestAnimationFrame(this.animate);
        },

        init() {
            scene = new THREE.Scene();

            loadModels();
            loadGeometry();

            addCamera();
            //                addSceneHelpers();

            initRenderer();
            initEvents();

            render();
            this.animate();

            stats.showPanel(0); // 0: fps, 1: ms, 2: mb, 3+: custom
            document.body.appendChild(stats.dom);
            addGui();
        }
    },
};

</script>

<style scoped>
.Scene {
    flex: 1;
    z-index: 10;
    /*background-color: #181919;*/
    position: absolute;
    width: 100vw;
    height: 100vh;
    color: #FFF;
    align-content: center;
    justify-content: center;
}

.Scene::after {
    content: "";
    /*background: url('/static/wpt.jpg');*/
    background-size: cover;
    opacity: 0.25;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: 0;
}
</style>



